<script>
  let menuVisible = false;

  function preventDefault(e) {
    e = e || window.event;
    if (e.preventDefault) e.preventDefault();
    e.returnValue = false;
  }
  function preventDefaultForScrollKeys(e) {
    let keys = { 37: 1, 38: 1, 39: 1, 40: 1 };
    if (keys[e.keyCode]) {
      preventDefault(e);
      return false;
    }
  }
  let showMenu = () => {
    menuVisible = true;

    document.addEventListener("touchmove", preventDefault, { passive: false }); // Disable scrolling in Chrome
    window.onwheel = preventDefault; // modern standard
    window.onmousewheel = document.onmousewheel = preventDefault; // older browsers, IE
    window.ontouchmove = preventDefault; // mobile
    document.onkeydown = preventDefaultForScrollKeys;



  };

  let hideMenu = () => {
    menuVisible = false;

    document.removeEventListener("touchmove", preventDefault, {
      passive: false
    }); // Enable scrolling in Chrome
    window.onmousewheel = document.onmousewheel = null;
    window.onwheel = null;
    window.ontouchmove = null;
    document.onkeydown = null;
  };

  import { onMount } from "svelte";

  onMount(async () => {
    // Google code
    window.dataLayer = window.dataLayer || [];
    function gtag() {
      dataLayer.push(arguments);
    }
    gtag("js", new Date());
    gtag("config", "UA-147626348-1");

    // logo longclick
    let node = document.getElementById("lagarsoftLogo");
    let longpress = false;
    let presstimer = null;
    let longtarget = null;

    let cancel = function(e) {
      if (presstimer !== null) {
        clearTimeout(presstimer);
        presstimer = null;
      }

      this.classList.remove("longpress");
    };

    let click = function(e) {
      if (presstimer !== null) {
        clearTimeout(presstimer);
        presstimer = null;
      }

      this.classList.remove("longpress");

      if (longpress) {
        return false;
      }
    };

    let start = function(e) {
      if (e.type === "click" && e.button !== 0) {
        return;
      }

      longpress = false;
      this.classList.add("longpress");
      presstimer = setTimeout(function() {
        window.location = "img/header-logo.svg";
        longpress = true;
      }, 1000);
      return false;
    };

    node.addEventListener("mousedown", start);
    node.addEventListener("touchstart", start);
    node.addEventListener("click", click);
    node.addEventListener("mouseout", cancel);
    node.addEventListener("touchend", cancel);
    node.addEventListener("touchleave", cancel);
    node.addEventListener("touchcancel", cancel);

  });
</script>

<nav class="flex items-center justify-between flex-wrap p-6 lg:p-16">
  <div class="flex items-center flex-shrink-0 text-white">
    <a href="/">
      <img
        class="w-48 sm:w-64"
        src="img/header-logo.svg"
        id="lagarsoftLogo"
        title="..."
        alt="Lagarsoft logo" />
    </a>
  </div>
  <div class="block">
    <button
      class="flex items-center px-3 py-2 text-lgsftDarkBlue md:text-white z-30"
      on:click={showMenu}>
      <span class="hidden sm:inline-block text-sm tracking-widest z-30">
        MENU  
      </span>
      <svg
        class="fill-current h-5 w-5 z-30"
        viewBox="0 0 20 20"
        xmlns="http://www.w3.org/2000/svg">
        <title>Menu</title>
        <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
      </svg>
    </button>
  </div>

</nav>
{#if menuVisible}
  <div
    id="fixed"
    class="fixed inset-0 z-40 "
    style="background-color: #2A2D6F; opacity: 0.98;">
    <button
      class="ml-auto mt-4 mr-4 text-white flex items-center px-3 py-2"
      on:click={hideMenu}>
      <span class="hidden sm:inline-block text-sm tracking-widest">
        CLOSE  
      </span>
      <svg
        class="fill-current h-5 w-5"
        viewBox="0 0 25 25"
        xmlns="http://www.w3.org/2000/svg">
        <title>Close</title>
        <path fill="#fff" d="M0 20.8L20.8 0 24 3.3 3.3 24z" />
        <path fill="#fff" d="M3.2 0L24 20.7 20.7 24 0 3.3z" />
      </svg>
    </button>

    <container class="w-full absolute bottom-0 ">
      <a class="hover:underline" href="/" on:click={hideMenu}>
        <div
          class="ml-6 md:w-48 md:h-48 md:ml-12 "
          style="background-color: #E7462C; border-radius:50%; width: 14vh;
          height: 14vh; padding-top: 4vh; " />
      </a>
      <ul
        id="menu"
        class="block leading-extraLoose tracking-widest text-white ml-6 md:ml-12
        font-light overflow-hidden text-4vh sm:text-6vh "
        >
        <li>
          <a class="hover:underline" href="/about-us" on:click={hideMenu}>
            ABOUT&nbsp;US
          </a>
        </li>
        <li>
          <a class="hover:underline" href="/services" on:click={hideMenu}>
            SERVICES
          </a>
        </li>
        <li>
          <a class="hover:underline" href="/work" on:click={hideMenu}>WORK</a>
        </li>
        <li>
          <a class="hover:underline" href="/lab" on:click={hideMenu}>LAB</a>
        </li>
        <li>
          <a class="hover:underline" href="/contact-us" on:click={hideMenu}>
            CONTACT
          </a>
        </li>
      </ul>
    </container>

  </div>
{/if}
